.wrap{
    width: 400px;
    height: 200px;
    position: absolute;
    right: 0;
    top: 0;
    transition: all linear .5s;
}
.weather{
    border-radius: 4px;
    padding: 6px 10px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-size: 16px;
    transition: all linear .3s;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.tips{
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 40px;
    opacity: 0;
    transition: all linear .8s;
}
.wrap:hover .weather{
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}
.wrap:hover .tips{
    opacity: 1;
}