body,html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#root{
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchBox{
    width: 670px;
    height: 50px;
    border-bottom: 1px solid #ddd;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -335px;
    margin-top: -50px;
    transition: all linear .3s;
}
.searchBox:hover{
    box-shadow: 2px 2px 20px rgba(241, 14, 71, 0.6);
    margin-top: -60px;
}
.searchInput{
    width: 100%;
    height: 100%;
    outline: none;
    box-sizing: border-box;
    border: none;
    padding: 0 20px;
    color: #333;
    font-size: 16px;
    font-family: 'Microsoft Yahei';
}
.wrap{
    width: 400px;
    height: 200px;
    position: absolute;
    right: 0;
    top: 0;
    transition: all linear .5s;
}
.weather{
    border-radius: 4px;
    padding: 6px 10px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-size: 16px;
    transition: all linear .3s;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.tips{
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 40px;
    opacity: 0;
    transition: all linear .8s;
}
.wrap:hover .weather{
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}
.wrap:hover .tips{
    opacity: 1;
}
.App{
    width: 100%;
    height: 100%;
    /* background-image:url('./static/background.jpg'); */
    background-size: cover; 
    overflow: hidden;
}
